body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  background: #f4f4f4;
}

.App {
  text-align: center;
  padding: 20px;
}

.App-header {
  background: #007bff;
  color: white;
  padding: 20px;
}

form {
  margin: 20px auto;
  max-width: 300px;
  text-align: left;
}

button {
  margin-top: 10px;
  background: #007bff;
  color: white;
  border: none;
  padding: 8px 12px;
  cursor: pointer;
  border-radius: 4px;
}

button:hover {
  background: #0056b3;
}

h3, h4 {
  color: #333;
}

p {
  font-size: 16px;
  color: #555;
}
